import Vue from 'vue'
import VueRouter from 'vue-router'
import store from '../store'
import * as types from '../store/mutation'
// import Home from '../components/Home'  // @/components/login
// import Login from '../components/Login'
// import SignUp from '../components/SignUp'
// import UpdatePassword from '../components/UpdatePassword'
// import Question from '../components/Question'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'newscnn',
    component: function (resolve) {
      require(['../components/Newscnn.vue'], resolve)
    }
  },
  {
    path: '/login',
    name: 'Login',
    component: function (resolve) {
      require(['../components/Login.vue'], resolve)
    }
  },
  {
    path: '/donate',
    name: 'Donate',
    meta: {
      requireAuth: false  // 
    },
    component: function (resolve) {
      require(['../components/Donate.vue'], resolve)
    }
  },
  {
    path: '/starme',
    name: 'Starme',
    meta: {
      requireAuth: false  // 
    },
    component: function (resolve) {
      require(['../components/StarMe.vue'], resolve)
    }
  },
  {
    path: '/signUp',
    name: 'signUp',
    component: function (resolve) {
      require(['../components/SignUp.vue'], resolve)
    }
  },
  {
    path: '/updatePassword',
    name: 'updatePassword',
    meta: {
      requireAuth: false  // 
    },
    component: function (resolve) {
      require(['../components/UpdatePassword.vue'], resolve)
    }
  },
  {
    path: '/home',
    name: 'Home',
    meta: {
      requireAuth: true  // 
    },
    component: function (resolve) {
      require(['../components/Home.vue'], resolve)
    }
  },
  {
    path: '/question',
    name: 'Question',
    meta: {
      requireAuth: false  // 
    },
    component: function (resolve) {
      require(['../components/Question.vue'], resolve)
    }
  },
  {
    path: '/questionlist',
    name: 'QuestionList',
    meta: {
      requireAuth: false  // 
    },
    component: function (resolve) {
      require(['../components/QuestionList.vue'], resolve)
    }
  },
  {
    path: '/redeemDetail',
    name: 'RedeemDetail',
    meta: {
      requireAuth: true  // 
    },
    component: function (resolve) {
      require(['../components/RedeemDetail.vue'], resolve)
    }
  },
  {
    path: '/questionDetail',
    name: 'QuestionDetail',
    meta: {
      requireAuth: false  // 
    },
    component: function (resolve) {
      require(['../components/questionDetail.vue'], resolve)
    }
  },
  {
    path: '/regulation',
    name: 'Regulation',
    meta: {
      requireAuth: true  // 
    },
    component: function (resolve) {
      require(['../components/Regulation.vue'], resolve)
    }
  },
  {
    path: '/personCenter',
    name: 'PersonCenter',
    meta: {
      requireAuth: true  // 
    },
    component: function (resolve) {
      require(['../components/PersonCenter.vue'], resolve)  // 
    }
  },
  {
    path: '/redeem',
    name: 'Redeem',
    meta: {
      requireAuth: true  // 
    },
    component: function (resolve) {
      require(['../components/Redeem.vue'], resolve)  // 
    }
  },
  {
    path: '/setting',
    name: 'Setting',
    meta: {
      requireAuth: false  // 
    },
    component: function (resolve) {
      require(['../components/Setting.vue'], resolve)  // 
    }
  },
  {
    path: '/withdraw',
    name: 'Withdraw',
    meta: {
      requireAuth: false  // 
    },
    component: function (resolve) {
      require(['../components/Withdraw.vue'], resolve)  // 
    }
  },
  {
    path: '/productDetail',
    name: 'ProductDetail',
    meta: {
      requireAuth: false  // 
    },
    component: function (resolve) {
      require(['../components/ProductDetail.vue'], resolve)  // 
    }
  },
  {
    path: '/productRedeemDetail',
    name: 'ProductRedeemDetail',
    meta: {
      requireAuth: true  // 
    },
    component: function (resolve) {
      require(['../components/ProductRedeemDetail.vue'], resolve)  // 
    }
  },
  {
    path: '/redeemInstruction',
    name: 'RedeemInstruction',
    meta: {
      requireAuth: true  // 
    },
    component: function (resolve) {
      require(['../components/RedeemInstruction.vue'], resolve)  // 
    }
  },
  {
    path: '/addressInfo',
    name: 'AddressInfo',
    meta: {
      requireAuth: true  // 
    },
    component: function (resolve) {
      require(['../components/AddressInfo.vue'], resolve)  // 
    }
  },
  {
    path: '/payment',
    name: 'Payment',
    meta: {
      requireAuth: true  // 
    },
    component: function (resolve) {
      require(['../components/Payment.vue'], resolve)  // 
    }
  },
  {
    path: '/paygift',
    name: 'Paygift',
    meta: {
      requireAuth: true  // 
    },
    component: function (resolve) {
      require(['../components/Paygift.vue'], resolve)  // 
    }
  }
]

// token state
if (window.localStorage.getItem('token')) {
  store.commit(types.LOGIN, window.localStorage.getItem('token'))
}
const router = new VueRouter({
  routes
})

// simple history management
const history = window.sessionStorage
history.clear()
let historyCount = history.getItem('count') * 1 || 0
history.setItem('/', 0)
let isPush = false
let endTime = Date.now()
let methods = ['push', 'go', 'replace', 'forward', 'back']

document.addEventListener('touchend', () => {
  endTime = Date.now()
})
methods.forEach(key => {
  let method = router[key].bind(router)
  router[key] = function (...args) {
    isPush = true
    method.apply(null, args)
  }
})

// control the url redirect to the login
router.beforeEach((to, from, next) => {
  store.commit(types.UPDATE_LOADING, true)
  // store.commit('updateLoadingStatus', {isLoading: true})
  const toIndex = history.getItem(to.path)
  const fromIndex = history.getItem(from.path)
  if (toIndex === fromIndex) {
    store.commit(types.UPDATE_LOADING, false)
  }
  if (toIndex) {
    if (!fromIndex || parseInt(toIndex, 10) > parseInt(fromIndex, 10) || (toIndex === '0' && fromIndex === '0')) {
      store.commit(types.UPDATE_DIRECTION, {direction: 'forward'})
    } else {
      // ios
      if (!isPush && (Date.now() - endTime) < 377) {
        store.commit(types.UPDATE_DIRECTION, {direction: ''})
      } else {
        store.commit(types.UPDATE_DIRECTION, {direction: 'reverse'})
      }
    }
  } else {
    ++historyCount
    history.setItem('count', historyCount)
    to.path !== '/' && history.setItem(to.path, historyCount)
    store.commit(types.UPDATE_DIRECTION, {direction: 'forward'})
  }
  if (/\/http/.test(to.path)) {
    let url = to.path.split('http')[1]
    window.location.href = `http${url}`
  } else {
    next()
  }
  // old
  // store.commit(types.UPDATE_LOADING, true)
  if (to.matched.some(r => r.meta.requireAuth)) {
    if (store.state.login.token) {
      // alert(store.state.login.token)
      // console.log('333333' + store.state.login.token)
      next()
    } else {
      // alert('no token')
      next({
        path: '/login',
        query: {redirect: to.fullPath}
      })
    }
  } else {
    next()
  }
})
router.afterEach(function (to) {
  store.commit(types.UPDATE_LOADING, false)
})
export default router



