import Vue from 'vue'

// const HOST = 'http://192.168.1.10:3000'
const HOST = 'http://egoend1.tunnel.qydev.com'
export default {
  install () {
    Vue.prototype.$apis = {
      login: '/login',
      logout: '/logout',
      register: '/register', // 
      answer: '/question/answer', // answer question
      userInfo: '/user/info', // 
      todayQuestion: '/question/today', // view today question  parmas:{isToday: true}
      getRandomCode: '/getRandomCode', // question params {isToday=true}
      updatePassword: '/updatePassword', // reset password
      giftList: '/userGift/list',  // gift list
      questionStoreList: '/questionstore/list', // user right question wrong question params {userId}
      questionById: '/question/byId', // get one question by id  params{questionId}
      getGiftById: '/gift/getById', // get one question by id  params{questionId}
      saveBaseInfo: 'user/baseInfo', // save base address user info phone
      redeemGift: '/gift/redeem', // redeem gift now
      redeemGift2: '/gift/redeem2', // redeem gift now 2
      userGetRedeemed: '/gift/user/redeemedGifts', // user get redeemed gift by userId
      getExpressInfo: '/user/expressInfo',  // get express info by userId
      getRedeemOrderById: '/gift/user/getRedeemOrderById',  // get redeemOrderById info by orderId
      answerTimeout: '/question/answer/timeout',  // get redeemOrderById info by orderId
      userOffline: HOST + '/front/dUser/Offline', // 
      userGrade: HOST + '/front/dUserRole/query', // 
      upGrade: HOST + '/front/dUserRole/query', // 
      getCode: HOST + '/front/dUser/getCode', //    token Phone
      getProvice: HOST + '/Fmodules/dProvinces/query', //  
      getCity: HOST + '/Fmodules/dCity/query', //  provinceid
      getDistrict: HOST + '/Fmodules/dArea/query', //  District
      transform: HOST + '/front/dUser/transfer', //  token  type 1  2 3   money     account 
      convert: HOST + '/front/dUser/convert', //  token type=1 ,type=2 
      addBankCard: HOST + '/front/DBank/add', //   bankName account name  address
      myBankCard: HOST + '/front/DBank/query', // 
      deleteBankCard: HOST + '/front/DBank/delete', //   token   bankId
      withdraw: HOST + '/front/dUser/withdraw', //  ID bankId  money token
      withdrawLog: HOST + '/front/dWithdrawalApplication/query', //    token  pageNo   pageSize
      netUsers: HOST + '/front/dLayerFigure/query',  // 
      netQueryById: HOST + '/front/dLayerFigure/queryByAccount',  //  account  ,token
      myMessage: HOST + '/front/message/query',  //  token
      sendMessage: HOST + '/front/message/send',  //    token  remark userAccount
      convertAndTransLogs: HOST + '/front/dRevenueRecord/query', //   token type2 1  2  pageNo pageSize
      deposit: HOST + '/front/dPay/deposit',  //   token type  123  money ,note  ,tupian  
      depositLogs: HOST + '/front/dPay/query',  //   token   state  1 2:3  pageNo  pageSize
      orderUsers: HOST + '/front/dUser/queryFrom',  // token type pageNo PsgeSize
      products: HOST + '/modules/dProduct/query',  //   id
      activeateRadio: HOST + '/front/dUser/queryActivateRatio',   //   token account
      activeAccount: HOST + '/front/dUser/activation', //  //token, account, registerCurrency, activateCurrency
      withdrawRadio: HOST + '/modules/dRule/query',  // 
      revenueRecord: HOST + '/front/dRevenueRecord/query'  //   type 1;23

    }
  }
}



